.row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  word-break: break-word;
  padding: 1rem 0;
  border-bottom: 1px solid #3F3F47; }
  .row:first-child {
    padding-top: 0; }
  .row:last-child {
    padding-bottom: 0;
    border-bottom: 0; }

.proxyMissing .text {
  color: white; }

.proxyMissing .accountIcon :global(.account-icon) {
  fill: white; }

.proxyMissing .warningIcon :global(.warning-icon) {
  fill: white; }

.proxyAvailable .text {
  color: #69F0AE; }

.proxyAvailable .accountIcon :global(.account-icon) {
  fill: #69F0AE; }

.proxyAvailable .warningIcon :global(.warning-icon) {
  fill: #69F0AE; }

.warning .text {
  color: #97979B;
  font-size: .8125rem;
  letter-spacing: .1px;
  line-height: 18px; }

.warning .warningIcon {
  height: 1.375rem;
  width: 1.375rem;
  flex-shrink: 0;
  margin-right: .8125rem;
  margin-left: 0; }
  .warning .warningIcon :global(.warning-icon) {
    fill: #97979B; }

.allowances .text {
  font-size: 0.875rem;
  color: #97979B;
  letter-spacing: 0.4px; }

.text {
  font-size: .875rem;
  letter-spacing: .82px; }

.accountIcon {
  width: 24px;
  height: 24px;
  margin-right: 1rem; }

.warningIcon {
  height: 18px;
  width: 18px;
  margin-left: 6px; }

.button {
  height: auto;
  font-size: 12px;
  font-weight: 600;
  letter-spacing: 0.2px;
  padding: 6px 15px; }

.placeholder {
  min-height: 27px;
  display: flex;
  align-items: center; }



