.container {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  width: 100%;
  padding: 0 30px 0 0; }
  @media (max-width: 480px) {
    .container {
      padding: 0; } }

.dropdown {
  position: relative; }
  @media (max-width: 480px) {
    .dropdown {
      width: 100%; } }

@media (max-width: 480px) {
  .mobileWrapper {
    display: flex;
    flex-direction: column-reverse;
    margin: auto;
    align-items: flex-end; } }

.dropdownBtn {
  height: 48px;
  width: 220px;
  border-right: 1px solid rgba(255, 255, 255, 0.1);
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  background: url("../../icons/chevron-down.svg") no-repeat 186px center; }
  @media (max-width: 480px) {
    .dropdownBtn {
      border-right: none; } }

.dropdownBtnDisabled {
  cursor: default;
  background-image: none; }

.dropdownBtnActive {
  box-shadow: inset 1px 1px 7px 2px rgba(0, 0, 0, 0.3); }

.dropdownList {
  position: absolute;
  z-index: 10;
  width: 100%;
  top: 48px;
  list-style: none;
  padding: 0;
  margin: 0;
  background-color: #2F2F38;
  border: 1px solid #2B2B33;
  border-top: none;
  box-shadow: 0 0 4px 1px rgba(0, 0, 0, 0.2);
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px; }

.dropdownItem {
  display: block; }

.dropdownItem:not(:last-child) {
  border-bottom: 1px solid #2B2B33; }

.dropdownItemLink {
  display: flex;
  color: white;
  text-decoration: none; }
  .dropdownItemLink:hover {
    background-color: #393942;
    cursor: pointer; }

.pairView {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 26px 12px 12px;
  width: 100%; }

.pairViewCurrency {
  flex-basis: 50px;
  flex-grow: 1; }

.pairViewIcon {
  height: 26px;
  width: 26px;
  margin-right: 20px;
  display: flex;
  align-items: center; }

.active {
  font-weight: bold; }

.passive {
  font-weight: normal; }

.pairInfo {
  padding: 0 16px;
  flex-grow: 1;
  display: flex;
  align-items: center; }
  @media (max-width: 480px) {
    .pairInfo {
      padding: 10px 16px;
      flex-direction: column-reverse;
      align-items: flex-end;
      border-top: 1px solid #2B2B33; } }

.pairInfoLabel {
  margin-left: 7px; }
  @media (max-width: 480px) {
    .pairInfoLabel {
      font-size: 10px;
      margin-left: 0; } }

@media (max-width: 480px) {
  .pairInfoValue {
    font-size: 14px; } }



