.section {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 100vh;
  width: 100vw;
  margin: auto; }
  .section h1, .section h2, .section h3, .section h4, .section h5, .section h6 {
    margin: 0;
    font-weight: 500; }

.logo {
  height: 2.5em;
  width: 10em;
  margin-bottom: 2.625em; }

.container {
  width: 100%;
  max-width: 490px;
  padding: 24px;
  border: 1px solid #555561;
  border-radius: 4px; }
  .containerTopHalf {
    display: inline-flex;
    text-transform: capitalize;
    align-items: center;
    width: 100%;
    padding-bottom: 24px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.05); }
    .containerTopHalf .placeholder {
      display: inline-flex;
      justify-content: space-between; }
    .containerTopHalf .column {
      margin-left: 12px;
      display: inline-flex;
      align-items: flex-start;
      flex-direction: column; }
  .containerBottomHalf {
    width: 100%;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    margin-top: 24px; }
    .containerBottomHalf a {
      text-decoration: none;
      color: #BBDEFB; }

.label {
  font-family: Montserrat, sans-serif;
  font-weight: 500;
  margin-right: 4px;
  letter-spacing: .4px;
  color: #8D8D96; }
  .label.client {
    line-height: 17px;
    color: #FFF; }
  .label.status {
    line-height: normal;
    font-size: 12px;
    text-align: left; }

.availableClients {
  display: flex;
  flex-direction: row;
  width: 100%;
  margin-top: 32px; }
  @media (max-width: 480px) {
    .availableClients {
      flex-direction: column; } }
  .availableClients > *:first-child {
    margin-left: 0; }
  .availableClients > * {
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 1 1 100%;
    margin-left: 24px; }
    @media (max-width: 480px) {
      .availableClients > * {
        margin-left: 0;
        margin-bottom: 16px;
        max-width: 100%; } }



