.table {
  display: table;
  table-layout: fixed;
  border-collapse: collapse;
  width: 100%; }
  @media (max-width: 480px) {
    .table {
      max-height: calc(100vh - 70px) !important; }
      .table th {
        position: sticky;
        top: -1px;
        background-color: #393942; } }
  .table th {
    font-size: 12px;
    text-transform: uppercase;
    font-weight: 600;
    letter-spacing: 0.43px; }
  .table td {
    font-family: Lato, sans-serif;
    font-weight: 600;
    font-size: 14px; }
  .table th, .table td {
    padding: 8px 28px 8px 0; }
  .table td.numerical {
    overflow: hidden;
    -ms-text-overflow: ellipsis;
    text-overflow: ellipsis; }
  @media (max-width: 480px) {
    .table th, .table td,
    .table th:first-child,
    .table td:first-child {
      padding-right: 16px; }
    .table th:last-child,
    .table td:last-child {
      padding-right: 24px; } }

.extendOnBorders {
  margin-left: -1px;
  width: calc(100% + 2px); }

td.right,
th.right,
.right td,
.right th {
  text-align: right; }

td.center,
th.center,
.center td,
.center th {
  text-align: center; }

td.left,
th.left,
.left td,
.left th {
  text-align: left; }

.trHighlighted, .table thead {
  background-color: #393942; }
  .trHighlighted th, .table thead th, .trHighlighted td, .table thead td {
    border-top: 1px solid #2B2B33;
    border-bottom: 1px solid #2B2B33; }

.clickable {
  cursor: pointer; }
  .clickable:hover {
    background-color: #33333b; }
  .clickable:active {
    background-color: #3f3f49; }



