.container {
  min-height: 230px; }

.orderTypeBtn {
  width: 86px; }
  @media (max-width: 360px) {
    .orderTypeBtn {
      width: 72px;
      font-size: 11px; } }

td.right,
th.right,
.right td,
.right th {
  text-align: right; }

.myTradesTable {
  max-height: 400px; }
  .myTradesTable td:first-child, .myTradesTable th:first-child {
    padding-left: 16px; }
    @media (min-width: 992px) {
      .myTradesTable td:first-child, .myTradesTable th:first-child {
        padding-left: 24px;
        width: 100px; } }

.myOpenTradesTable th:nth-child(1), .myOpenTradesTable td:nth-child(1) {
  max-width: 100px; }

.myOpenTradesTable th:nth-child(2), .myOpenTradesTable td:nth-child(2) {
  max-width: 165px; }

.myOpenTradesTable th:nth-child(3), .myOpenTradesTable td:nth-child(3) {
  max-width: 165px; }

.myOpenTradesTable th:nth-child(4), .myOpenTradesTable td:nth-child(4) {
  max-width: 165px; }

.myOpenTradesTable th:nth-child(5), .myOpenTradesTable td:nth-child(5) {
  max-width: 165px; }

.myOpenTradesTable th:nth-child(6), .myOpenTradesTable td:nth-child(6) {
  max-width: 165px; }

@media (max-width: 992px) {
  .myOpenTradesTable th:nth-child(1), .myOpenTradesTable td:nth-child(1) {
    max-width: 50px; } }

@media (max-width: 992px) {
  .myOpenTradesTable th:nth-child(2), .myOpenTradesTable td:nth-child(2) {
    max-width: 85px; } }

@media (max-width: 992px) {
  .myOpenTradesTable th:nth-child(3), .myOpenTradesTable td:nth-child(3) {
    max-width: 85px; } }

@media (max-width: 992px) {
  .myOpenTradesTable th:nth-child(4), .myOpenTradesTable td:nth-child(4) {
    max-width: 85px; } }

@media (max-width: 992px) {
  .myOpenTradesTable th:nth-child(5), .myOpenTradesTable td:nth-child(5) {
    max-width: 60px; } }

@media (max-width: 992px) {
  .myOpenTradesTable th:nth-child(6), .myOpenTradesTable td:nth-child(6) {
    max-width: 75px; } }

.myOpenTradesTable td:last-child {
  white-space: nowrap; }

.myCloseTradesTable th:nth-child(1), .myCloseTradesTable td:nth-child(1) {
  max-width: 100px; }

.myCloseTradesTable th:nth-child(2), .myCloseTradesTable td:nth-child(2) {
  max-width: 165px; }

.myCloseTradesTable th:nth-child(3), .myCloseTradesTable td:nth-child(3) {
  max-width: 165px; }

.myCloseTradesTable th:nth-child(4), .myCloseTradesTable td:nth-child(4) {
  max-width: 165px; }

.myCloseTradesTable th:nth-child(5), .myCloseTradesTable td:nth-child(5) {
  max-width: 165px; }

@media (max-width: 992px) {
  .myCloseTradesTable th:nth-child(1), .myCloseTradesTable td:nth-child(1) {
    max-width: 60px; } }

@media (max-width: 992px) {
  .myCloseTradesTable th:nth-child(2), .myCloseTradesTable td:nth-child(2) {
    max-width: 100px; } }

@media (max-width: 992px) {
  .myCloseTradesTable th:nth-child(3), .myCloseTradesTable td:nth-child(3) {
    max-width: 100px; } }

@media (max-width: 992px) {
  .myCloseTradesTable th:nth-child(4), .myCloseTradesTable td:nth-child(4) {
    max-width: 100px; } }

@media (max-width: 992px) {
  .myCloseTradesTable th:nth-child(5), .myCloseTradesTable td:nth-child(5) {
    max-width: 90px; } }

.status {
  display: inline-flex;
  align-items: center;
  justify-content: flex-end;
  width: 100%; }

.statusText {
  display: inline-block;
  margin-right: 1em;
  text-transform: uppercase; }

.statusProgress {
  display: inline-block;
  position: relative; }



