.orderbook {
  width: 452px;
  height: 100%;
  overflow: hidden; }
  @media (max-width: 992px) {
    .orderbook {
      width: 100%; } }

.switchBtn {
  margin-right: 0;
  padding: 0;
  width: 30px;
  height: 30px;
  fill: white; }

.spreadRow {
  background-color: rgba(255, 255, 255, 0.02); }

.orderbookTable {
  max-height: 360px; }
  .orderbookTable th:nth-child(1), .orderbookTable td:nth-child(1) {
    max-width: 146px; }
  .orderbookTable th:nth-child(2), .orderbookTable td:nth-child(2) {
    max-width: 146px; }
  .orderbookTable th:nth-child(3), .orderbookTable td:nth-child(3) {
    max-width: 146px; }
  .orderbookTable td:last-child, .orderbookTable th:last-child {
    flex-grow: 1; }

:global(.order-enter) {
  opacity: 0.01; }

:global(.order-enter-active) {
  opacity: 1;
  transition: opacity 1000ms ease-in; }

:global(.order-exit) {
  opacity: 1; }

:global(.order-exit-active) {
  opacity: 0.01;
  transition: opacity 1000ms ease-in; }



