@media (max-width: 800px) {
  :global(.paper) {
    height: 20px !important;
    width: 20px !important; } }

.header {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  margin-top: 1em;
  margin-bottom: 0.75em; }
  .header .logo {
    width: 132px;
    max-height: 37px;
    margin: auto 1.5em auto auto; }
    .header .logo svg:hover :global(#bg) {
      fill-opacity: 0.35; }

.section {
  display: inline-flex; }
  .sectionNavigation {
    margin-right: auto; }
    @media (max-width: 800px) {
      .sectionNavigation {
        order: 3;
        width: 100%; } }
  @media (max-width: 800px) {
    .sectionStatus {
      flex: 1 1 auto;
      justify-content: flex-end; } }

@media (max-width: 800px) {
  .nav, .list, .item {
    width: 100%; } }

.list {
  display: flex;
  list-style-type: none;
  padding: 0; }
  @media (max-width: 800px) {
    .list {
      margin-top: 24px; } }
  .list .item + .item {
    margin-left: -1px; }
    .list .item + .item .navLink {
      border-left-color: transparent; }
  .list .item:first-child, .list .item:first-child > * {
    border-bottom-left-radius: 4px;
    border-top-left-radius: 4px; }
  .list .item:last-child, .list .item:last-child > * {
    border-bottom-right-radius: 4px;
    border-top-right-radius: 4px; }
  .list .item:last-child {
    margin-right: 0; }

.navElement, .navLink {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  height: 40px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  color: #fff;
  border-radius: 0; }

.navLink {
  text-decoration: none;
  width: 108px;
  color: #87878C; }
  .navLink:hover {
    background-color: rgba(255, 255, 255, 0.1);
    cursor: pointer; }
  @media (max-width: 800px) {
    .navLink {
      width: 100%; } }

.activeNavLink, .activeNavLink:hover {
  color: white;
  border: 1px solid #87878C !important;
  background: initial; }

.account {
  width: 196px;
  border-radius: 4px;
  margin-right: 0px; }
  @media (max-width: 800px) {
    .account {
      width: 32px;
      height: 32px; } }
  @media (max-width: 800px) {
    .account span {
      display: none; } }

.accountLoader {
  display: flex;
  margin-right: 12px; }

.login {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%; }
  @media (max-width: 800px) {
    .login {
      padding: 0; } }

.connectButton {
  text-transform: none;
  font-size: 16px; }

:global(.Popover.noWallet) :global(.Popover-tip) {
  fill: #fff; }

:global(.Popover.noWallet) :global(.Popover-body) {
  background-color: #fff;
  border-color: #fff;
  border-radius: 2px;
  color: black;
  padding: 1em; }

.networkIndicator {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 79px;
  height: 40px;
  background: transparent;
  border-radius: 4px;
  font-family: 'Montserrat', sans-serif;
  font-weight: 500;
  font-size: 16px;
  letter-spacing: 1.4px;
  text-transform: capitalize; }
  @media (max-width: 800px) {
    .networkIndicator {
      max-width: 68px;
      font-size: 14px;
      height: 32px; } }

.networkIndicator.kovan {
  color: #B388FF;
  border: 1px solid #B388FF; }

.networkIndicator.main {
  color: #69F0AE;
  border: 1px solid #69F0AE; }



