.fallBar,
.fallStick {
  fill: #FF6951;
  background-color: #FF6951; }

.riseStick {
  fill: #1CBA80;
  background-color: #1CBA80; }

.riseBar {
  fill: #2c3d3f;
  background-color: #2c3d3f;
  stroke: #1CBA80;
  stroke-width: 1; }

.equal {
  fill: #87878C;
  background-color: #87878C; }

.volume {
  fill: #393942;
  background-color: #393942; }

.bands rect {
  fill: darkgrey;
  fill-opacity: 0.3;
  stroke-opacity: 0;
  pointer-events: all;
  shape-rendering: crispEdges; }

.bands .hoved {
  fill-opacity: .6; }

:global(.candles) .hoved {
  stroke-width: 2; }
  :global(.candles) .hoved.riseBar {
    stroke: #00d687; }
  :global(.candles) .hoved.fallBar {
    stroke: #ffa799; }
  :global(.candles) .hoved.equal {
    stroke: #87878C; }

:global(.sticks) .hoved.riseStick {
  stroke: #07cf86; }

:global(.sticks) .hoved.fallStick {
  stroke: #ffa799; }

:global(.volume) .hoved {
  fill: #58585F; }

.axisYMainLabel {
  font-family: Lato, sans-serif;
  font-size: 14px;
  font-weight: 700;
  color: #87878C; }

.axisYVolumeLabel {
  font-family: Lato, sans-serif;
  color: #87878C;
  font-size: 12px;
  font-weight: 500; }

.axisXMainLabel {
  font-family: Montserrat, sans-serif;
  font-weight: 500;
  color: #fff;
  font-size: 13px; }

.axisLineMain {
  stroke: #636369;
  stroke-width: 2px; }

.axisYVolumeLineMain {
  stroke-linecap: round; }

.axisLineGrid {
  stroke: #383842;
  stroke-width: 1px; }

.infoBox {
  position: absolute;
  left: auto;
  top: 0;
  background-color: rgba(47, 47, 56, 0.5);
  font-size: 10px;
  padding: 2px 3px;
  display: flex;
  justify-content: space-between;
  width: calc(100% - 20px);
  max-width: 440px; }

.infoBoxItem {
  width: 200px; }
  @media (max-width: 480px) {
    .infoBoxItem {
      width: auto; } }
  @media (max-width: 360px) {
    .infoBoxItem {
      font-size: 9px; } }



