.inputGroup {
  display: flex;
  background-color: #2F2F38; }
  .inputGroup > * {
    border-top: 1px solid #58585F;
    border-bottom: 1px solid #58585F; }
  .inputGroup > *:first-child {
    border-top-left-radius: 2px;
    border-bottom-left-radius: 2px;
    border-left: 1px solid #58585F; }
  .inputGroup > *:last-child {
    border-top-right-radius: 2px;
    border-bottom-right-radius: 2px;
    border-right: 1px solid #58585F; }

.addon {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 5px 20px;
  font-size: 14px; }

.xs {
  height: 26px;
  font-size: 14px; }

.sm {
  height: 30px;
  font-size: 14px; }

.md {
  height: 32px;
  font-size: 14px; }

.lg {
  height: 38px;
  font-size: 14px; }

.unsized {
  height: auto;
  font-size: auto; }

.sm .addon, .sm input, .md .addon, .md input {
  font-size: 12px; }

.inputGroupAddonBorderRight {
  border-right: 1px solid;
  border-color: inherit; }

.inputGroupAddonBorderLeft {
  border-left: 1px solid;
  border-color: inherit; }

.grey {
  border-color: #58585F; }

.red {
  border-color: #FF6951; }

.disabled {
  border-color: #393942;
  color: #58585F; }



