.main {
  position: fixed;
  right: 1em;
  top: 1em;
  z-index: 10; }
  @media (max-width: 480px) {
    .main {
      right: 0;
      top: 0; } }

.block {
  position: relative;
  padding: 16px 24px 16px 16px;
  margin: 10px 18px 24px 18px;
  background-color: #2F2F38;
  border: 1px solid #2B2B33;
  border-radius: 4px;
  box-shadow: 0 0 4px 1px rgba(0, 0, 0, 0.2);
  font-size: 14px;
  width: 350px; }
  @media (max-width: 480px) {
    .block {
      margin: 0;
      border-radius: 0;
      width: 100vw; } }

.title {
  letter-spacing: 0.4px;
  margin-bottom: 4px;
  margin-top: 5px; }

.close {
  position: absolute;
  top: 0.5em;
  right: 0.5em;
  padding: 2px;
  border: 1px solid black;
  cursor: pointer; }
  .close:hover {
    background-color: #eee; }

.icon {
  width: 44px;
  height: 50px;
  float: left;
  margin-top: 7px; }

.icon-inner {
  width: 32px;
  height: 32px; }

.description {
  color: #58585F; }

.cross {
  border-radius: 100%;
  background-color: #393942;
  height: 1.5em;
  width: 1.5em;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 1em;
  right: 1em;
  cursor: pointer; }
  .cross:hover, .cross:focus {
    opacity: 0.8; }

:global(.transaction-enter) {
  opacity: 0.01; }

:global(.transaction-enter-active) {
  opacity: 1;
  transition: opacity 1000ms ease-in; }

:global(.transaction-exit) {
  opacity: 1; }

:global(.transaction-exit-active) {
  opacity: 0.01;
  transition: opacity 1000ms ease-in; }



