.links > *:after {
  content: "/";
  display: inline-block;
  margin-left: 0.5em;
  margin-right: 0.5em;
  color: rgba(255, 255, 255, 0.1); }

.links > *:last-child:after {
  content: ""; }

.footerSeparator {
  margin-top: 4em;
  margin-bottom: 2em;
  border: 1px solid rgba(255, 255, 255, 0.1); }

.footer {
  text-align: center;
  font-family: Montserrat, sans-serif;
  font-size: 14px;
  color: #B3B3B9;
  letter-spacing: 0.2px;
  margin-bottom: 2em; }
  .footer > * {
    margin-bottom: 16px; }
  .footer a {
    text-decoration: none;
    color: #B3B3B9; }
    .footer a:hover {
      color: #FFF; }
    .footer a.iconLink {
      display: inline-block;
      margin-right: 0.5em;
      height: 16px;
      vertical-align: top; }



