.infoIcon {
  color: #87878C;
  border: 1px solid #87878C;
  border-radius: 100%;
  width: 22px;
  height: 22px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  font-size: 12px;
  font-weight: bold; }

@keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg); } }

.progressIcon {
  display: inline-block;
  border: 2px solid #58585F;
  border-right-color: white;
  border-radius: 100%;
  width: 16px;
  height: 16px;
  animation: spin 3s linear infinite; }

.progressIconLight {
  border-color: #eee;
  border-right-color: #448AFF; }

.progressIconSm {
  width: 12px;
  height: 12px; }

.progressIconLg {
  width: 26px;
  height: 26px; }

.socialIcon path {
  fill: #B3B3B9; }

.socialIcon:hover path {
  fill: #FFF; }

.btnIcon {
  height: 100%;
  width: 100%;
  display: inline-flex;
  align-items: center;
  justify-content: center; }



