:global(.hide-all) {
  display: none !important; }

@media (max-width: 480px) {
  :global(.hide-md) {
    display: none !important; } }

@media (max-width: 992px) {
  :global(.hide-lg) {
    display: none !important; } }

:global(::placeholder) {
  color: #58585F; }

:global(.bold) {
  font-weight: 700 !important; }

:global(.semi-bold) {
  font-weight: 600 !important; }

:global(.medium) {
  font-weight: 500 !important; }

:global(.danger) {
  color: #EF5350; }



