.txStatusRow {
  display: flex;
  align-items: center;
  width: 100%;
  font-size: 0.75rem;
  margin-bottom: 1rem; }
  @media (max-width: 480px) {
    .txStatusRow {
      flex-wrap: wrap;
      justify-content: center;
      text-align: center; } }
  .txStatusRow:last-child {
    margin-bottom: 0; }
  .txStatusRow .icon {
    height: 1.5rem;
    width: 1.5rem;
    margin-right: 1rem;
    flex-shrink: 0; }
  .txStatusRow .label {
    min-width: 9rem;
    font-size: 0.75rem;
    letter-spacing: 0.7px; }
  .txStatusRow .status {
    margin-left: auto;
    letter-spacing: .5px; }
    @media (max-width: 480px) {
      .txStatusRow .status {
        display: flex;
        margin: 1rem auto;
        position: absolute;
        bottom: 0; } }



