.tradingPairPanel {
  flex-grow: 1; }

.priceChartPanel,
.allTradesPanel {
  width: 454px;
  height: 342px; }
  @media (max-width: 992px) {
    .priceChartPanel,
    .allTradesPanel {
      width: 100%; } }

.offerMakePanel {
  height: 474px;
  width: 454px;
  margin-right: 24px;
  flex-grow: 1; }
  @media (max-width: 992px) {
    .offerMakePanel {
      margin-right: 0;
      width: 100%;
      height: 522px; } }

.orderbookPanel {
  height: 474px;
  flex-basis: 454px;
  flex-grow: 1; }
  @media (max-width: 992px) {
    .orderbookPanel {
      width: 100%;
      height: 342px; } }

.myOrdersPanel {
  min-height: 284px;
  width: 100%; }



