.entry {
  display: flex;
  width: 100%;
  max-width: 12rem;
  justify-content: space-between;
  align-items: center; }
  .entry.reversed .label {
    color: rgba(255, 255, 255, 0.9); }
  .entry.reversed .value {
    color: #828287; }
  .entry.reversed .infoIcon {
    color: rgba(255, 255, 255, 0.9);
    border-color: rgba(255, 255, 255, 0.9); }
  @media (max-width: 480px) {
    .entry {
      max-width: none; } }

.label {
  font-family: Montserrat, sans-serif;
  font-weight: 500;
  font-size: 12px;
  color: #828287;
  letter-spacing: 1.1px; }

.value {
  font-family: Montserrat, sans-serif;
  font-weight: 500;
  font-size: 12px;
  color: rgba(255, 255, 255, 0.9);
  letter-spacing: 1px;
  text-align: right; }



