.container {
  width: 100%;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  border: 1px solid #3F3F47;
  border-radius: 4px;
  margin: 1.25rem 0 1rem;
  padding: 2.375rem 0;
  color: #fff;
  font-size: 14px; }
  @media (max-width: 360px) {
    .container {
      font-size: 12px; } }

.impactText {
  letter-spacing: 1px;
  text-align: center;
  color: #97979B;
  margin: 16px 0; }

.continueText {
  font-weight: 500;
  letter-spacing: 1px;
  text-align: center;
  margin: 0; }

.graph {
  width: 180px;
  margin-bottom: 32px; }

.bar {
  background: #55AC85;
  border: 1px solid #55AC85;
  border-radius: 1px;
  height: 20px;
  margin-top: 10px; }

.bar-1 {
  margin-top: 0;
  width: 60px; }

.bar-2 {
  width: 90px; }

.bar-3 {
  width: 135px; }

.bar-4 {
  width: 180px; }

.bar.danger {
  background: #AC4648;
  border: 1px solid #AC4648; }

.arrow-placeholder {
  display: inline-block;
  width: 45px;
  height: 80px; }

.arrow {
  position: relative;
  display: block;
  top: 0;
  width: 4px;
  height: 74px;
  background-color: #AC4648;
  margin: auto; }

.arrow:after {
  position: absolute;
  display: block;
  content: "";
  width: 0;
  height: 0;
  bottom: -6px;
  right: -4px;
  border-top: 6px solid #AC4648;
  border-left: 6px solid transparent;
  border-right: 6px solid transparent; }



