.wrapper {
  background-color: #58585F;
  border-radius: 10px;
  width: 34px;
  height: 20px;
  display: inline-flex;
  align-items: center;
  border: none;
  padding: 0;
  position: relative; }
  .wrapper:hover {
    cursor: pointer; }

.pointer {
  background-color: white;
  border-radius: 100%;
  height: 16px;
  width: 16px;
  margin: 2px;
  transition: all 0.5s ease-in-out; }

.disabled.wrapper {
  cursor: default; }

.disabled .pointer {
  background-color: #bbb; }

.pointerBlocked {
  margin-left: 16px;
  background: white url("./switch-disabled.svg") no-repeat 14% center; }

.pointerUnblocked {
  margin-left: 2px;
  background: white url("./switch-active.svg") no-repeat 84% center; }

.moveOnHover:not(.disabled):hover .pointerBlocked {
  margin-left: 2px;
  background: white url("./switch-active.svg") no-repeat 84% center; }

.moveOnHover:not(.disabled):hover .pointerUnblocked {
  margin-left: 16px;
  background: white url("./switch-disabled.svg") no-repeat 14% center; }

.inProgress {
  background-image: none; }

.progressIcon {
  position: absolute; }

.progressBlocked {
  right: 4px; }

.progressUnblocked {
  left: 4px; }



