.radio {
  position: relative;
  display: block; }
  .radio input[type="radio"] {
    display: none;
    margin-right: 0;
    margin-top: 0;
    position: relative; }
    .radio input[type="radio"][disabled] + span:before, .radio input[type="radio"][readonly] + span:before {
      border-color: #58585F;
      opacity: 1; }
    .radio input[type="radio"][disabled] + span:after, .radio input[type="radio"][readonly] + span:after {
      background-color: #58585F; }
    .radio input[type="radio"][disabled] + span, .radio input[type="radio"][readonly] + span {
      color: #7d7d87; }
    .radio input[type="radio"] + span:before {
      content: "";
      position: absolute;
      left: -30px;
      top: 2px;
      box-sizing: border-box;
      width: 16px;
      height: 16px;
      border: 1.49px solid white;
      background-color: transparent; }
    .radio input[type="radio"]:checked + span:after {
      position: absolute;
      left: -26px;
      top: 6px;
      z-index: 2;
      content: '';
      width: 8px;
      height: 8px;
      background-color: white;
      border: none; }
  .radio input[type="radio"] + span:before,
  .radio input[type="radio"] + span:after {
    border-radius: 50%; }

.hasError {
  background-color: red; }



