.taxExporterDescription {
  flex-grow: 1;
  font-size: 14px;
  display: block;
  margin-top: 8px;
  margin-bottom: 8px; }
  @media (max-width: 360px) {
    .taxExporterDescription {
      font-size: 12px; } }
  .taxExporterDescription span {
    color: #fff;
    display: block;
    font-size: 15px;
    margin-bottom: 8px; }
    @media (max-width: 360px) {
      .taxExporterDescription span {
        font-size: 14px; } }
  .taxExporterDescription a {
    color: #6fc2e9; }

.taxExporterPanelBody {
  display: flex;
  width: 100%;
  border-top: 1px solid #393942; }

.taxExporterButton {
  margin-top: 8px; }
  @media (max-width: 360px) {
    .taxExporterButton {
      margin-top: 0;
      align-self: center; } }

.progressIcon {
  position: relative;
  width: 14px;
  height: 14px;
  top: 2px;
  margin: 0 25px; }



