.allTradesTable {
  max-height: 254px; }
  .allTradesTable th:nth-child(1), .allTradesTable td:nth-child(1) {
    max-width: 152px; }
  .allTradesTable th:nth-child(2), .allTradesTable td:nth-child(2) {
    max-width: 152px; }
  .allTradesTable th:nth-child(3), .allTradesTable td:nth-child(3) {
    max-width: 142px; }
  .allTradesTable td:last-child, .allTradesTable th:last-child {
    flex-grow: 1;
    white-space: nowrap; }
  .allTradesTable .loadMore {
    padding-left: 24px; }

.loader {
  display: inline-block;
  width: 16px;
  min-height: 16px;
  margin-right: 4px;
  background-size: contain;
  background-image: url("../../utils/loadingIndicator/LoadingIndicatorDark.gif");
  background-repeat: no-repeat;
  background-position: center;
  opacity: 0.5; }

:global(.trade-enter) {
  opacity: 0.01; }

:global(.trade-enter-active) {
  opacity: 1;
  transition: opacity 1000ms ease-in; }

:global(.trade-exit) {
  opacity: 1; }

:global(.trade-exit-active) {
  opacity: 0.01;
  transition: opacity 1000ms ease-in; }



