.borderBox {
  border: 1px solid #58585F;
  border-radius: 4px; }

.smPadding {
  padding: 10px 12px; }

.mdPadding {
  padding: 14px; }

.nonePadding {
  padding: 0; }

.hr {
  border: none;
  border-bottom: 1px solid;
  clear: both;
  width: 100%;
  margin: 0; }

.lightHr {
  border-color: rgba(255, 255, 255, 0.2); }

.darkHr {
  border-color: #393942; }



