.depthChartWithLoading {
  width: 508px; }
  @media (max-width: 992px) {
    .depthChartWithLoading {
      width: 100%; } }

.depthChart {
  position: relative;
  display: inline-block; }

.sellChart {
  fill: #FF6951;
  background-color: #FF6951; }

.sellChartDark {
  fill: #C15849;
  background-color: #C15849; }

.buyChart {
  fill: #1CBA80;
  background-color: #1CBA80; }

.buyChartDark {
  fill: #22906A;
  background-color: #22906A; }

.hidden {
  visibility: hidden; }

.axisMainLabel {
  font-family: Lato, sans-serif;
  font-size: 14px;
  font-weight: 700; }

.axisLineMain {
  stroke: #636369;
  stroke-width: 2px; }

.axisLineAdditional {
  stroke: #444;
  stroke-width: 1px; }

.axisYLineAdditional {
  stroke: #444;
  stroke-width: 0.5; }

.axisYTokenLabel {
  font-family: Montserrat, sans-serif;
  font-weight: 600;
  color: #87878C;
  font-size: 10px; }

.infoBox {
  background-color: #2F2F38;
  border: 1px solid #FF6951;
  display: table;
  font-size: 10px;
  padding: 8px;
  position: absolute; }

.infoBoxRed {
  border-color: #D55D4C;
  stroke: #D55D4C; }

.infoBoxGreen {
  border-color: #1F9E71;
  stroke: #1F9E71; }

.sellText {
  color: #FF6951; }

.buyText {
  color: #1CBA80; }

.infoSummary {
  display: table-row; }

.infoSummaryLine {
  padding-top: 8px;
  padding-bottom: 8px;
  display: table-cell;
  white-space: nowrap; }
  .infoSummaryLine:first-child {
    padding-right: 10px; }

.infoSummaryLegendImg {
  margin-right: 8px;
  margin-top: 3px; }

.legendImg {
  height: 9px;
  width: 12px; }

.smallLegend {
  align-items: center;
  background-color: #2F2F38;
  display: inline-flex;
  justify-content: flex-end;
  font-size: 10px;
  padding: 4px 6px;
  position: absolute; }

.dotGlow {
  fill: rgba(255, 255, 255, 0.1); }

.dotSell {
  fill: #F06856;
  stroke-width: 1;
  stroke: white; }

.dotBuy {
  fill: #1CBA80;
  stroke-width: 1;
  stroke: white; }



